export class ApiController {
    constructor(api) {
        this.api = api;
    }
    async getCardsData() {
        try {
            const rawCardsData = await this.api.get('/product/');
            return rawCardsData;
        }
        catch (e) {
            console.log(e);
            return {
                items: [],
                count: 0
            };
        }
    }
    async postOrder(order) {
        try {
            const orderResponse = await this.api.post('/order', order);
            return orderResponse;
        }
        catch (e) {
            console.log(e);
            return null;
        }
    }
}
