export class OrderView {
    constructor(templateOrderForm, templateOrderContactForm, templateSuccess, events) {
        this.templateOrderForm = templateOrderForm;
        this.templateOrderContactForm = templateOrderContactForm;
        this.events = events;
        this.orderFormElement = this.templateOrderForm.content.querySelector('form').cloneNode(true);
        this.orderContactFormElement = this.templateOrderContactForm.content.querySelector('form').cloneNode(true);
        this.successElement = templateSuccess.content.querySelector('.order-success').cloneNode(true);
    }
    getOrderForm() {
        const cardButton = this.orderFormElement.querySelector('button[name="card"]');
        const cashButton = this.orderFormElement.querySelector('button[name="cash"]');
        const addressInput = this.orderFormElement.querySelector('input[name="address"]');
        cardButton.addEventListener('click', () => {
            this.events.emit('order:setPaymentType', { paymentType: 'card' });
        });
        cashButton.addEventListener('click', () => {
            this.events.emit('order:setPaymentType', { paymentType: 'cash' });
        });
        addressInput.addEventListener('input', (event) => {
            const inputElement = event.target;
            this.events.emit('order:setAddress', { address: inputElement.value });
        });
        this.orderFormElement.addEventListener('submit', (event) => {
            event.preventDefault();
            this.events.emit('order:changeStage', { orderStage: 'selectEmailPhone' });
        });
        return this.orderFormElement;
    }
    getOrderContactForm() {
        const emailInput = this.orderContactFormElement.querySelector('input[name="email"]');
        const phoneInput = this.orderContactFormElement.querySelector('input[name="phone"]');
        emailInput.addEventListener('input', (event) => {
            const inputElement = event.target;
            this.events.emit('order:setEmail', { email: inputElement.value });
        });
        phoneInput.addEventListener('input', (event) => {
            const inputElement = event.target;
            this.events.emit('order:setPhone', { phone: inputElement.value });
        });
        this.orderContactFormElement.addEventListener('submit', (event) => {
            event.preventDefault();
            this.events.emit('order:finish');
        });
        return this.orderContactFormElement;
    }
    setPaymentType(paymentType) {
        const cardButton = this.orderFormElement.querySelector('button[name="card"]');
        const cashButton = this.orderFormElement.querySelector('button[name="cash"]');
        cashButton.style.border = '';
        cardButton.style.border = '';
        if (paymentType === 'card')
            cardButton.style.border = '2px solid white';
        if (paymentType === 'cash')
            cashButton.style.border = '2px solid white';
    }
    setErrorText(errorText) {
        const orderFormErrorsField = this.orderFormElement.querySelector('.form__errors');
        orderFormErrorsField.textContent = errorText;
        const orderContactFormErrorsField = this.orderContactFormElement.querySelector('.form__errors');
        orderContactFormErrorsField.textContent = errorText;
    }
    setContinueButtonActive(active) {
        const continueButton = this.orderFormElement.querySelector('.order__button');
        continueButton.disabled = active;
    }
    setFinishButtonActive(active) {
        const finishButton = this.orderContactFormElement.querySelector('button[type="submit"]');
        finishButton.disabled = active;
    }
    getSuccessElement(order) {
        this.successElement.querySelector('.order-success__description').textContent = `Списано ${order.total} синапсов`;
        this.successElement.querySelector('.order-success__close').addEventListener('click', () => this.events.emit('order:success'));
        return this.successElement;
    }
    getFormElement(stage) {
        return stage === 'selectPaymentType' ? this.getOrderForm() : this.getOrderContactForm();
    }
}
